	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Indy!","date":"04 Feb 2008","slug":"/2008-02-04-indy/","tags":"Y2008"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2241098453\" title=\"View 'CIMG0449' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2204/2241098453_971cd5a87e.jpg\" alt=\"CIMG0449\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>Christopher as Indiana Jones, Fasching 2008.</p>"},"next":{"fields":{"title":"Road Home","date":"27 Jan 2008","slug":"/2008-01-27-road-home/"}},"previous":{"fields":{"title":"At Least They Have Snakes","date":"08 Feb 2008","slug":"/2008-02-08-at-least-they-have-snakes/"}}},{"node":{"fields":{"title":"Road Home","date":"27 Jan 2008","slug":"/2008-01-27-road-home/","tags":"Y2008"},"html":"<p>My brother is fully awake now and has been moved to the telemetry ward and is no longer in intensive care. Telemetry means in this case he's attached to the monitors via WLAN instead of being hard-wired, or something like that. He'll have a second stent put in on Monday and is scheduled to be discharged on Tuesday.</p>\n<p>As for me, I've booked a return flight to be back home Tuesday evening, just in time for report-card school vacation that starts on Wednesday. Thanks again for all the support online.</p>"},"next":{"fields":{"title":"Sheriff Christopher","date":"26 Jan 2008","slug":"/2008-01-26-sheriff-christopher/"}},"previous":{"fields":{"title":"Indy!","date":"04 Feb 2008","slug":"/2008-02-04-indy/"}}},{"node":{"fields":{"title":"Sheriff Christopher","date":"26 Jan 2008","slug":"/2008-01-26-sheriff-christopher/","tags":"Y2008"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2220668240\" title=\"View 'Fwd: Sheriff Christopher' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2071/2220668240_0639805c06.jpg\" alt=\"Fwd: Sheriff Christopher\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>Nice to see Christopher is keeping busy while I'm away.</p>"},"next":{"fields":{"title":"Out of the Woods","date":"25 Jan 2008","slug":"/2008-01-25-out-of-the-woods/"}},"previous":{"fields":{"title":"Road Home","date":"27 Jan 2008","slug":"/2008-01-27-road-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":85,"pageCount":1101}}

